.form {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  
  &__item {
   margin-bottom: 8px; 
  }

  &__input, &__button {
    width: 100%;
   
  }

  &::placeholder {
    color: inherit;
  }

  &__input {
    height: 54px;
    background-color: #f0efef;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 14px;
    line-height: 36px;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
      -webkit-appearance: none;
  }
  input[type='number'] {
      -moz-appearance: textfield;
  }

  &__textarea {
    height: 156px;
    resize: none;
  }
  
  &__button {
    height: 54px;
    max-width: 100%;
    background-color: #00AAEF;
    font-weight: 500;
    font-size: 14px;
    line-height: 36px;
    color: white;
    text-transform: uppercase;
    &:hover {
      box-shadow: 0 0 10px rgba(0,0,0,0.5);
    }
  }
}
